#paragraph1
{
    width: 70%;
}

.homepageStructure
{
    display: grid;
    grid-template-columns: 60% 40%;
}

.paragraphText
{
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    font-size: 18px;
    color: grey;
    line-height: 150%;
}

.widthSmallHome
{
    display: none;
}

@media only screen and (max-width: 1300px) {
    #paragraph1
    {
        width: 80%;
    }
}

@media only screen and (max-width: 1050px) {
    .homepageStructure
    {
        grid-template-columns: 100%;
    }
    .widthLarge
    {
        display: none;
    }
    .widthSmallHome
    {
        display: inline;
    }
}